.cmb2-id--give-agree-text {
  display: none;
}

/*-------------------------------------
Dashboard Columns
-------------------------------------*/

input.shortcode-input {
  width: 100%;
}

/*-------------------------------------
ASIDE
-------------------------------------*/

#side-sortables {

  .cmb-th label {

    &:after {
      display: none;
    }

  }
  .cmb2-id--give-show-register-form {
    padding: 10px 0 0 !important;
  }

  .cmb2-wrap > .cmb-field-list > .cmb-row {
    padding-top: 10px;
  }

  .cmb-row .cmb2-metabox-description {
    padding-bottom: 0;
  }

}

.cmb2-metabox-description {
  font-size: 13px;
  line-height: 1.5;
  display: block;
}

//-------------------------------------
// Tooltips
//-------------------------------------
.give-tooltip {
  margin: 0 3px;
  padding: 0;
  font-size: 14px;
  @include easing-transition();
  color: transparentize($dark, 0.5);
  position: relative;
  top: 1px;

  &:hover {
    color: $dark;
  }

}

//-------------------------------------
// Metabox Form
// -------------------------------------
#give-metabox-form-data {

  .inside {
    margin: 0;
    padding: 0;
  }

  .give-metabox-panel-wrap {
    background: #fff;
    overflow: hidden;
  }

  .give_options_panel {
    padding-left: 170px;

    .give-field-wrap {
      padding: 1em 20px 1em 162px !important;
      margin: 0;
      border-bottom: 1px solid #eee;

      > label, > span.give-field-label {
        float: left;
        width: 135px;
        padding: 0;
        margin: 2px 0 0 -150px;
        line-height: 18px;
        font-size: 12px;
        font-weight: 400;
      }

      .give-field-description {
        color: #aaaaaa;
        font-size: 12px;
        line-height: 18px;
        display: block;
        padding-top: 10px;
        font-style: italic;
      }

      ul {
        margin: 0;
      }

      .mce-container iframe, textarea {
        min-height: 250px !important;
      }
    }

    .give-field-wrap:last-child {
      border-bottom: 0;
    }

    textarea.give-field {
      width: 100%;
    }

    .give-field-wrap:hover .give-field-row-actions {
      display: block;
    }

    .give-field-wrap:last-child {
      border-bottom: 0;
    }

    .give-inline-radio-fields {
      li {
        display: inline-block;
        margin-right: 15px;
      }
    }

    .give-repeatable-field-section {
      padding: 10px;
      background: #f5f5f5;

      .give-repeatable-fields-section-wrapper {
        width: 100%;
        background-color: white;
        border-collapse: collapse;
      }

      .give-row {
        border-bottom: 15px solid #f5f5f5;
        background-color: white;
      }

      .give-template {
        display: none;
      }

      .give-row:last-child {
        border-bottom: 10px solid #f5f5f5;
      }

      .give-column {
        padding: 0;
      }

      .give-remove {
        float: right;
        width: 36px;
        height: 36px;
        padding: 0;
        display: block;
        text-align: center;
        line-height: 34px;
        font-size: 21px;
        font-weight: 300;
        cursor: pointer;
      }

      .give-remove:hover {
        background-color: red;
        color: white;
        width: 30px;
        height: 30px;
        border-radius: 30px;
        margin-top: 3px;
        line-height: 28px;
      }

      .give-row-head {
        border-bottom: 1px solid #eee;
        cursor: move;
        h2 {
          text-align: left !important;
        }
      }

      .give-row.closed .toggle-indicator:before {
        content: "\f140";
      }

      .give-add-repeater-field-section-row-wrap {
        background-color: #f5f5f5;
      }

      .give-add-repeater-field-section-row {
        margin: 5px 5px;
      }

      .give-field-wrap:last-child {
        border-bottom: 1px solid #eee;
      }

      .give-ui-placeholder-state-highlight {
        background-color: #68bb6c;
      }

      .give-repeater-field-name {
        border: 0 !important;
        padding: 0 !important;
      }

      .give-repeater-field-description {
        color: #aaaaaa;
        font-size: 12px;
        line-height: 18px;
        display: block;
        padding-bottom: 10px;
        font-style: italic;
      }

      // Sortable jquery css fix.
      .ui-sortable-helper {
        display: table !important;
      }
      .give-row.ui-sortable-helper {
        border-bottom: 0;
      }
    }
  }
}

//-------------------------------------
// Metabox Tabs
//-------------------------------------
#give-metabox-form-data {

  .give-metabox-tabs {
    margin: 0;
    min-width: 170px;
    float: left;
    line-height: 1em;
    padding: 0 0 10px;
    position: relative;
    background-color: #fafafa;
    border-right: 1px solid #eee;
    box-sizing: border-box;

    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 9999em;
      position: absolute;
      bottom: -9999em;
      left: 0;
      background-color: #fafafa;
      border-right: 1px solid #eee;
    }

    li {
      margin: 0;
      padding: 0;
      display: block;
      position: relative;

      a {
        margin: 0;
        padding: 10px;
        display: block;
        box-shadow: none;
        text-decoration: none;
        line-height: 20px !important;
        border-bottom: 1px solid #eee;
        overflow: hidden;

        span {
          width: 122px;
          display: inline-block;
        }
      }

    }

    .give-metabox-sub-tabs li {
      background-color: #f1f1f1;
      a {
        border-bottom-color: #e5e5e5;
      }
    }

    li.active {
      > a {
        color: #555;
        position: relative;
        background-color: #eee;
      }
    }

    li.has-sub-fields.active > a {
      background-color: #ddd;
    }

    li.has-sub-fields:not(.active):hover {
      ul.give-metabox-sub-tabs {
        display: block;
        position: absolute;
        bottom: 0;
        left: 170px;
        width: 170px;
        max-height: 250px;
        overflow-y: scroll;
      }
    }
  }

}

// Icons only display for tight resolutions.
@media only screen and (min-width: 851px) and (max-width: 1025px),
(max-width: 550px) {

  #give-metabox-form-data {
    ul.give-metabox-tabs {
      min-width: 40px;

      li > a {
        text-align: center;
        &:before {
          margin-right: 0;
          font-size: 15px;
        }
        span {
          display: none;
        }
      }

    }
    .give_options_panel {
      padding-left: 40px;
    }

  }
  //#give-metabox-form-data
}


